<template>
  <v-container id="data-list" fluid>
    <v-container fluid class="cyan grey lighten-3">
      <v-layout row>
        <v-flex xs12 md12 class="text-xs-center">
          <div class="headline">Data Cuaca</div>
          <div class="body-1">Menampilkan data cuaca yang tersedia dalam sistem. Anda bisa melakukan perubahan data.</div>
          <v-btn to="/app/add-data" large dark depressed color="indigo darken-2">
            Tambah
            <v-icon right>create</v-icon>
          </v-btn>
        </v-flex>
      </v-layout>
    </v-container>

    <v-data-table
      :headers="headers"
      :items="items"
    >
      <template v-slot:items="props">
        <td>{{props.item.tanggal}}</td>
        <td>{{props.item.suhu}}</td>
        <td>{{props.item.tekananUdara}}</td>
        <td>{{props.item.kecepatanAngin}}</td>
        <td>{{props.item.kelembapanUdara}}</td>
        <td>{{props.item.keterangan}}</td>
        <td>
          <v-menu offset-y>
            <template v-slot:activator="{ on }">
              <v-icon v-on="on">more_horiz</v-icon>
            </template>
            <v-list dense>
              <v-list-tile @click="openEditView(props.item._id)">
                <v-list-tile-sub-title>Edit</v-list-tile-sub-title>
              </v-list-tile>
              <v-list-tile @click="deleteData(props.item._id)">
                <v-list-tile-sub-title>Delete</v-list-tile-sub-title>
              </v-list-tile>
            </v-list>
          </v-menu>
        </td>
      </template>
    </v-data-table>
  </v-container>
</template>

<script>
import moment from "moment";
import { headers } from './headers';
import evbus from "@/evbus";
import { Cuaca__find, Cuaca__delete } from "@/services/cuaca";

export default {
  data: () => ({
    headers,
    items: []
  }),
  methods: {
    async loadData() {
      evbus.$emit("loading-on");
      try {
        let items = await Cuaca__find();
        items = items.map(it => ({
          ...it,
          tanggal: moment(it.tanggal).format("dddd, MMMM Do YYYY")
        }))
        this.items = items;
        evbus.$emit("success", "Sukses memuat data");
      } catch (err) {
        evbus.$emit("error", "Gagal memuat data");
      } finally {
        evbus.$emit("loading-off");
      }
    },
    openEditView(id) {
      this.$router.push(`/app/edit-data/${id}`);
    },
    deleteData(id) {
      const message = "Apakah anda yakin akan menghapus data #" + id;
      const action = async () => {
        try {
          await Cuaca__delete(id);
          evbus.$emit("success", "Sukses menghapus data");
        } catch (err) {
          evbus.$emit("error", "Gagal menghapus data");
        } finally {
          evbus.$emit("loading-off");
          this.loadData();
        }
      };
      evbus.$emit("app-action", { message, action });
    }
  },
  mounted() {
    this.loadData();
  }
}
</script>

